	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"13 Weeks = 3 Months","date":"27 Mar 2000","slug":"/2000-03-27-13-weeks-3-months/","tags":"Y2000"},"html":"<p>Christopher is 13 weeks old today, which happens to also be exactly 3 months. Time to start counting in months instead of weeks! </p>\n<p>He's starting to have some loud crying phases. Our first few weeks of the quiet baby with no problems to speak of were indeed too good to be true. Is he teething? Maybe... anyway, he and Mama are exhausted this evening. She won't be flipping her home page today.</p>\n<p>Meanwhile, we're getting ready for my folks to visit their new grandson for 2 1/2 weeks starting on Friday. We're having Christopher baptized this Sunday, and otherwise we have no fixed plans for their stay. We can't make any big trips... niether of our cars can accomodate 4 adults and a child seat!</p>"},"next":{"fields":{"title":"Daylight Savings Time Sunday","date":"26 Mar 2000","slug":"/2000-03-26-daylight-savings-time-sunday/"}},"previous":{"fields":{"title":"We're No Angels","date":"28 Mar 2000","slug":"/2000-03-28-were-no-angels/"}}},{"node":{"fields":{"title":"Daylight Savings Time Sunday","date":"26 Mar 2000","slug":"/2000-03-26-daylight-savings-time-sunday/","tags":"Y2000"},"html":"<p><rules><br />\n\t<rule><br />\n\t\t<outlinespacing>1.5</outlinespacing><br />\n\t\t</rule><br />\n\t</rules><br />\nIsn't switching to Daylight Savings Time great? You're an hour late before you even wake up.<br />\n<rules><br />\n\t<rule level=\"1\" to=\"1\"><br />\n\t\t<textstyle>bold</textstyle><br />\n\t\t</rule><br />\n\t</rules></p>"},"next":{"fields":{"title":"Washing windows, waiting for Pike","date":"25 Mar 2000","slug":"/2000-03-25-washing-windows-waiting-for-pike/"}},"previous":{"fields":{"title":"13 Weeks = 3 Months","date":"27 Mar 2000","slug":"/2000-03-27-13-weeks-3-months/"}}},{"node":{"fields":{"title":"Washing windows, waiting for Pike","date":"25 Mar 2000","slug":"/2000-03-25-washing-windows-waiting-for-pike/","tags":"Y2000"},"html":"<p>Hey... I've got it, and I'm writing this with \"Pike\". This is cool! Mama was just complaining today how hard it is to edit her page with the browser. (But I had to dial-in under Windows to get it to work, since my Linux router acts like a firewall. Guess I have to read up on port forwarding with IP masquarading....)</p>\n<p>It's quarter past 10 Pacific time... where's Pike? \";->\" (10:40... ah, <a href=\"http://pikebeta.userland.com\">here</a> it is!)</p>\n<p>Yahoo Deutschland: <a href=\"http://de.sports.yahoo.com/000325/6/no1z.html\">HSV auf Kurs in die Champions League</a>:  3rd place Hamburg defeats 4th place 1860 Munich 2-0 at home to move 8 points ahead. HSV is now 6 points behind frontrunners Bayern Munich and Leverkusen, who both managed just ties.</p>"},"next":{"fields":{"title":"Now... where were we?","date":"23 Mar 2000","slug":"/2000-03-23-now-where-were-we/"}},"previous":{"fields":{"title":"Daylight Savings Time Sunday","date":"26 Mar 2000","slug":"/2000-03-26-daylight-savings-time-sunday/"}}}],"pathPrefix":"","first":false,"last":false,"index":1070,"pageCount":1101}}

